.info{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    &-quest{
        margin-top: 0;
        font-family: var(--font-family-montsterrat);
        text-align: center;
    }
    &-search{
       width: 80%;
       display: block;
       margin: 0 auto;
       height: 40px;
       border-radius: 25px;
       border: none;
       color: #606f81;
       padding-left: 15px;
       background-image: url(../images/research.png);
       background-repeat: no-repeat;
       background-size: inherit;
       background-position: right;
       background-size: 20px 20px; 
    }
}
.categori{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 10px;
    &-block{
        display: flex;
        flex-direction: row;
         
    }
    &-but{
        margin-left: 10px;
        margin-right: 10px;
        border-radius: 15px;
        background: var(--gray);
        // height: 25px;
        padding: 5px 14px;
        &:hover{
            background: var(--yellow);
        }
         
    }
    &-but__link{
       color: var(--white);
    }
}

.citi-wrapper{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    padding: 0;
    margin-bottom:0 ;
}
.citi{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 350px;
    width: 340px;
    background: #fff;
    border-radius: 5px;
    
    text-align: center;
    font-family: var(--font-family-montsterrat);
    &-mg{
       padding: 0;
       margin: 0;
    }
    &-img{
        margin-top: 0;
        height: 160px;
        width: 336px;
        border-radius: 7px;
    }
    &-name{
        margin-top: 30px;
        margin-bottom: 8px;
        font-size: 30px;
        color: var(--gray);
    }
    &-decription{
        margin-top: 0;
        margin-bottom: 30px;
        color: var(--gray);
    }
    &-but{
        margin-top: 0;
        margin-bottom: 30px; 
    }
}