.explorer-wrap{
    height: 100vh;
    background: var(--bgraund);
    font-family: var(--font-family-montsterrat);
    display: flex;
    flex-direction: column;
    
}
.exp-bg-text{
    margin: 0;
    font-size: 150px;
    display: block;
    opacity: 0.7;
    color: var(--white);
}
.explorer{
    margin-left: 5px;
    margin-right: 5px;
    background: white;
    border-radius: 10px;
    // width: 360px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    
    &-img{
        text-align: center;
        width: 330px;
        height: 200px;
        border-radius: 8px;
    }
  
}
